


























































.title{
  margin-top: 20px;
}
.input-group{
  margin: 20px auto;
  width: 300px;
}
.list-group .item{
  margin: 0 auto 20px;
  width: 500px;
}
.list-group .form-control{
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  width: 100%;
}
.tip{
  display: none;
}
@media screen and (max-width: 768px){
  .list-group .item{
    width: 90%;
  }
  .input-group{
    width: 90%;
  }
}



